<template>
  <div id="newsItem" class="newsItem">
    <el-carousel
      height="544px"
      direction="horizontal"
      :autoplay="true"
      :interval="18000"
      arrow="never"
      class="el-carousel"
    >
      <el-carousel-item v-for="(item,index) in list" :key="index">
        <img :src="item.img" class="newsItem-img" alt=" " />
      </el-carousel-item>
    </el-carousel>

    <div :class="(this.$route.path!='/NEWS')? 'newsItem-centent-artists':'newsItem-centent-news'">
      <div class="newsItem-centent-bg">
        <strong>
          <p class="newsItem-title">MV </p>
        </strong>
        <h4 class="newsItem-time">2019.12.16 </h4>
        <div class="newsItem-line" />
        <p>MV </p>
        <div class="newsItem-line" />
        <p>
          MV
          MV
          MVMV2020MVMV1215YouTube
        </p>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  data() {
    return {
      list: [
        { img: require("../assets/artists/4000x4000.jpg") },
        { img: require("../assets/artists/J001.jpg") },
        { img: require("../assets/artists/J001.jpg") }
      ]
    };
  },
  computed: {
    artistsInfo() {
      return this.$store.state.artistsInfo;
    }
  },
  components: {}
};
</script>

<style lang="stylus" scoped>
.newsItem
  margin 0 28px
  padding 0
  width 100%
  height 580px
.el-carousel
  width 45%
  height auto
  margin 18px 0 0 0
  background-color #000
  display inline-block
.newsItem-img
  width 100%
  margin 0
  padding 0
  border 0
  outline 0
  font-size 100%
  font-style normal
  vertical-align baseline
.newsItem-centent-artists
  width 46%
  height 75%
  display inline-block
  position absolute
  top 36.5%
  right 6%
  overflow hidden
.newsItem-centent-news
  width 46%
  height 75%
  display inline-block
  position absolute
  top 27%
  right 6%
  overflow hidden
.newsItem-centent-bg
  width 100%
  height 100%
  overflow auto
.newsItem-title
  margin 0 0 15px 0
  font-size 36px
  line-height 40px
  font-weight 600
  color #313131
.newsItem-time
  color #b09869
  margin 0 0 12px 0
  padding 0
.newsItem-line
  height 3px
  margin 20px 0px
  background url('../assets/artists/news_line.png') left top repeat-x
.anewsItem-album
  margin 0
  font-size 14px
  line-height 28px
p
  font-size 16px
</style>




