<template>
  <div id="about" class="about">
    <PageTitle :info="info" />
    <div class="about-div">
      <b>
        <p class="about-title-jvr"></p>
      </b>
      <img src="../assets/about/about_hr.png" class="about-img" alt=" " />
      <p class="about-p" v-for="(item,index) in content" :key="index">{{item.p}}</p>
      <img src="../assets/about/about_hr.png" class="about-img" alt=" " />
    </div>
  </div>
</template>

<script type="text/javascript">
import PageTitle from "../components/PageTitle";
export default {
  name: "about",
  components: {
    PageTitle
  },
  data() {
    return {
      content: [
        {
          p:
            "JVR MusicJVRJayVincentJRFans"
        },
        {
          p:
            "JVR MusicJVRJVR=Just Very RomanticJVRJVRJVRJVRJVR!"
        },
        {
          p:
            "JVRJVRJVRFansJVR!"
        }
      ],
      info: {
        img: require("../assets/about/about_title.png"),
        title: "JVR"
      }
    };
  }
};
</script>

<style lang="stylus" scoped>
.about
  width 93.12%
  height 800px
  margin -7.7px 60px 60px 52.3px
  background-color #EDEDED
.about-title-img
  margin 30px 15px 0 30px
  width auto
  height auto
  display inline-block
.about-title
  position absolute
  top 127px
  font-size 28px
  color #000
  font-weight bold
  display inline-block
  letter-spacing 5px // 
.about-line-img
  width 95%
  height auto
  padding 0 30px
.about-div
  text-align center
.about-title-jvr
  margin-top 110px
  font-size 26px
  letter-spacing 2px
.about-img
  width 60px
  height auto
.about-p
  font-size 15.4px
  color #000
  padding 15px 230px
  text-align left
</style>



