<template>
  <div id="news" class="news">
    <PageTitle :info="info" />
    <div class="news-div" v-show="!showNews">
      <div class="news-box">
        <div class="news-grid">
          <div class="news-left">
            <div
              class="news-left-item"
              v-for="(item,index) in newsLeftdata"
              :key="index"
              @click="setNews(true)"
            >
              <img :src="item.img" class="news-left-img" alt=" " />
              <div class="news-left-mask">
                <div class="news-caption" />
                <p class="news-left-p">{{item.time}}</p>
                <p class="news-left-p1">{{item.centent}}</p>
              </div>
            </div>
          </div>
          <img src="../assets/news/news_list_line.jpg" class="news-list-line" alt=" " />
          <div class="news-right">
            <div
              class="news-right-item"
              v-for="(item,index) in newsRightdata"
              :key="index+10"
              @click="setNews(true)"
            >
              <img :src="item.img" class="news-right-img" alt=" " />
              <div class="news-item-mask">
                <div class="news-caption" />
                <p class="news-left-p">{{item.time}}</p>
                <p class="news-item-p1">{{item.centent}}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="news-bottom-box">
          <div
            class="news-bottom-item"
            v-for="(item,index) in bottomList"
            :key="index+20"
            @click="setNews(true)"
          >
            <img :src="item.img" class="news-bottom-img" alt=" " />
            <div class="news-item-mask">
              <div class="news-caption" />
              <p class="news-item-p">{{item.time}}</p>
              <p class="news-item-p1">{{item.centent}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <newsItem v-show="showNews"></newsItem>
  </div>
</template>

<script type="text/javascript">
import PageTitle from "../components/PageTitle";
import NewsItem from "../components/newsItem";
export default {
  name: "news",
  data() {
    return {
      info: {
        img: require("../assets/news/news_title.png"),
        title: ""
      },
      newsLeftdata: [
        {
          img: require("../assets/news/artist_news_young20200430_pin.png"),
          time: "2019.10.30 ",
          centent: ""
        },
        {
          img: require("../assets/news/856x387.jpg"),
          time: "2019.12.16 ",
          centent: "MV"
        }
      ],
      newsRightdata: [
        {
          img: require("../assets/news/cover.jpg"),
          time: "2019.10.30 ",
          centent: " DVD11/1"
        },
        {
          img: require("../assets/news/cover(1).jpg"),
          time: "2019.10.21 ",
          centent: " "
        },
        {
          img: require("../assets/news/cover(2).jpg"),
          time: "2019.10.18 ",
          centent: "20 "
        },
        {
          img: require("../assets/news/yyl.jpg"),
          time: "2019.10.09 ",
          centent:
            "Cindy23 TITAS"
        },
        {
          img: require("../assets/news/cover414250.jpg"),
          time: "2019.09.17 ",
          centent: " "
        },
        {
          img: require("../assets/news/list_news_4.jpg"),
          time: "2019.07.24 ",
          centent: "20 "
        }
      ],
      bottomList: [
        {
          img: require("../assets/news/414X250.jpg"),
          time: "2019.07.22 ",
          centent: ""
        },
        {
          img: require("../assets/news/artist_news_patrick20190606.png"),
          time: "2019.06.06 ",
          centent: ""
        },
        {
          img: require("../assets/news/yyl1.png"),
          time: "2019.05.16 ",
          centent:
            "Cindy2019 "
        },
        {
          img: require("../assets/news/list_cindy_news2.jpg"),
          time: "2019.01.17 ",
          centent: "MV "
        },
        {
          img: require("../assets/news/artist_news_cindy20190115.png"),
          time: "2019.09.16 ",
          centent: "2019  ......"
        },
        {
          img: require("../assets/news/artist_news_cindy20191108.png"),
          time: "2018.11.09 ",
          centent: "2018 Cindy"
        },
        {
          img: require("../assets/news/artist_news_cindy20180701.png"),
          time: "2018.07.01 ",
          centent: "Cindy......"
        },
        {
          img: require("../assets/news/bawjld.jpg"),
          time: "2018.05.14 ",
          centent: "......"
        }
      ]
    };
  },

  methods: {
    setNews(data) {
      this.$store.commit("setShowNews", data);
      this.$store.commit("setIconBack", true);
    }
  },
  computed: {
    showNews() {
      return this.$store.state.showNews;
    }
  },
  components: {
    PageTitle,
    NewsItem
  }
};
</script>

<style lang="stylus" scoped>
.news
  width 93%
  height 800px
  margin 0 60px 60px 52.3px
  background-color #EDEDED
.news-div
  width 95.95%
  height 650px
  margin 0 28px
  overflow hidden
.news-box
  width 100%
  height 650px
  margin auto
  overflow auto
.news-grid
  width 100%
  height 640px
  margin auto
  display grid
  grid-template-columns 49.25% 1.5% 49.25%
.news-left
  margin 8px 0 18px 0
  position relative
.news-list-line
  width 2px
  height 617px
  margin 8px 8px 20px 8px
.news-right
  margin 8px 0 18px 0
  position relative
.news-left-item
  position relative
  width 100%
  height 299px
  margin 0 0 19px 0
.news-left-img
  width 100%
  height 299px
.news-left-item:nth-child(2)
  margin 0px
.news-left-mask
  width 100%
  height 20%
  position absolute
  bottom 0
  z-index 10
  background-color #000
  opacity 1
.news-caption::before
  position absolute
  display block
  content ''
  width 4px
  height 80%
  left 11px
  top 8px
  background linear-gradient(90deg, #b4954f 35%, #d4bb7a 60%)
  background -moz-linear-gradient(90deg, #B4954F 35%, #D4BB7A 60%)
  background -webkit-linear-gradient(90deg, #B4954F 35%, #D4BB7A 60%)
.news-left-p
  position absolute
  top 8px
  left 22px
  margin 0
  padding 0
  font-size 12px
  color #c9b06b
.news-left-p1
  position absolute
  bottom 6px
  left 22px
  margin 0
  padding 0
  font-size 14px
  color #fff
  word-break break-all
  display -webkit-box
  -webkit-line-clamp 2 /*  */
  -webkit-box-orient vertical
  overflow hidden
.news-right-item
  width 49.23%
  height 199px
  position relative
  float left
  margin 0 10px 10px 0
  padding 0
.news-right-item:nth-child(2n)
  margin 0 0 10px 0
.news-right-img
  width 100%
  height 100%
  padding 0
.news-item-mask
  width 100%
  height 36%
  position absolute
  bottom 0
  z-index 10
  background-color #000
  opacity 1
.news-caption::before
  position absolute
  display block
  content ''
  width 4px
  height 80%
  left 11px
  top 8px
  background linear-gradient(90deg, #b4954f 35%, #d4bb7a 60%)
  background -moz-linear-gradient(90deg, #B4954F 35%, #D4BB7A 60%)
  background -webkit-linear-gradient(90deg, #B4954F 35%, #D4BB7A 60%)
.news-item-p
  position absolute
  top 8px
  left 22px
  margin 0
  padding 0
  font-size 12px
  color #c9b06b
.news-item-p1
  position absolute
  bottom 6px
  left 22px
  width 90%
  margin 0
  padding 0
  font-size 14px
  color #fff
  word-break break-all
  display -webkit-box
  -webkit-line-clamp 2 /*  */
  -webkit-box-orient vertical
  overflow hidden
.news-bottom-box
  width 100%
  height auto
  margin 0
  background-color blue
.news-bottom-item
  width 24%
  height 192px
  margin 0 1.33% 10px 0
  padding 0
  float left
  position relative
.news-bottom-img
  width 100%
  height 100%
  margin 0
.news-bottom-item:nth-child(4n)
  margin-right 0
</style>



